import { environment } from "../environments/environment";

import AxiosImp from "../utils/AxiosImp";

export default class AuthenticationService {
    axiosImp;

    constructor() {
        this.axiosImp = new AxiosImp();
    }

    validateAuthorization(autoriza, pais) {
        return this.axiosImp.post(environment.END_POINTS.VALIDATE_AUTHORIZATION, { parametro: autoriza, pais: pais });
    }
}
