import axios, { get, post, put } from "axios";
import { trackPromise } from "react-promise-tracker";

export default class AxiosImp {
    toast;

    constructor(toast) {
        this.toast = toast;
    }

    get(url, config) {
        console.log(url)
        return trackPromise(
            get(url)
                .then((response) => this.handleSuccess(response))
                .catch((error) => this.handleError(error))
        );
    }

    delete(url, config) {
        return trackPromise(
            axios
                .delete(url, config)
                .then((response) => this.handleSuccess(response))
                .catch((error) => this.handleError(error))
        );
    }

    post(url, data, config) {
        return post(url, data, config)
    }

    put(url, data, config) {
        return trackPromise(
            put(url, data, config)
                .then((response) => this.handleSuccess(response))
                .catch((error) => this.handleError(error))
        );
    }

    uploadFile(url, formFields, config) {
        const formData = new FormData();
        formFields.forEach((formField) => formData.append(formField.name, formField.value));

        return trackPromise(
            post(url, formData, {
                ...config,
                headers: {
                    "content-type": "multipart/form-data", // important
                },
            })
                .then((response) => this.handleSuccess(response))
                .catch((error) => this.handleError(error))
        );
    }

    downloadFile(url, config) {
        return trackPromise(
            get(url, {
                ...config,
                responseType: "blob", // important
            })
                .then((response) => this.handleDownloadSuccess(response))
                .catch((error) => this.handleError(error))
        );
    }

    handleSuccess(response) {
      //  console.log("handleSuccess", response);
        if (response.data.status === "ERROR") {
            this.showErrorMessage(response.data.errorCode + ": " + response.data.errorDescription);
            if (response.data.data) {
                console.log("handleSuccessWithError", response.data.data);
                return response.data.data;
            }
        } else {
            return response.data.data || response.data;
        }
    }

    handleDownloadSuccess(response) {
        if (response.data.type === "application/json") {
            let reader = new FileReader();
            reader.onload = (e) => {
                if (e.target.readyState === 2) {
                    let result = {};
                    result = JSON.parse(e.target.result);
                    this.showErrorMessage(result.errorCode + ": " + result.errorDescription);
                }
            };
            reader.readAsText(response.data);
        } else {
            return response;
        }
    }

    handleError(error) {
        console.error("handleError", error);
        if (!error.response) {
            this.showErrorMessage("Por favor revise su conexion a internet.");
        } else if (error.response.data.status === "ERROR") {
            this.showErrorMessage(error.response.data.errorCode + ": " + error.response.data.errorDescription);
        } else {
            this.showErrorMessage(error.message);
        }
    }

    showErrorMessage(detail) {
        if (this.toast?.current) {
            this.toast.current.show({ severity: "error", summary: "Error", detail: detail, life: 3000 });
        }
    }
}
