import { useState, useEffect } from "react";

import axios from "axios";

import { useQuery } from "../utils/params";
import { useHistory } from "react-router-dom";
import AuthenticationService from "../services/AuthenticationService";

export const useAuthorize = () => {

  const query = useQuery();
  const autoriza = query.get("autoriza");

  const history = useHistory();

  const [authenticatedUser, setAuthenticatedUser] = useState(null);

  useEffect(() => {


    const authenticationService = new AuthenticationService();
    authenticationService.validateAuthorization(autoriza, "").then((authUser) => {
      setAuthenticatedUser(authUser.data.data.user.nombre);
      axios.interceptors.request.use(function (config) {
        // TODO agregar request 
        if (authUser.data.data.user.token) {
          config.headers.Authorization = `Bearer ${authUser.data.data.user.token}`;
        }
        return config;
      });
    }).catch(error => {
      //history.push('/access')
      // TODO history.push()
    });

  }, [autoriza, history]);

  return { autoriza, authenticatedUser };
};
