import 'primereact/resources/primereact.min.css';
import 'primeicons/primeicons.css';
import './App.scss';

import classNames from "classnames";
import AppMenu from "./AppMenu";
import AppTopbar from './AppTopbar';
import { useState } from 'react';
import { useAuthorize } from './utils/authorize';
import { Route, withRouter } from "react-router-dom";
import { AgregarCampania } from './components/agregarCampania/AgregarCampania';
import { ListadoCampanias } from './components/listadoCampanias/ListadoCampanias';

const App = () => {

    // eslint-disable-next-line no-unused-vars
    const { autoriza, prueba, authenticatedUser } = useAuthorize();
    const [staticMenuDesktopInactive, setStaticMenuDesktopInactive] = useState(true);
    const [staticMenuMobileActive, setStaticMenuMobileActive] = useState(false);
    const [topbarMenuActive, setTopbarMenuActive] = useState(true);
    const [activeTopbarItem, setActiveTopbarItem] = useState(null);
    const [overlayMenuActive, setOverlayMenuActive] = useState(true);
    const [inputStyle] = useState('filled');
    const [ripple] = useState(false);
    const [profileMode] = useState('inline');
    const [menuMode] = useState('static');
    const [menuActive, setMenuActive] = useState(false);
    const [rotateMenuButton, setRotateMenuButton] = useState(true);
    /* let menuClick = false;*/

    const isHorizontal = () => {
        return menuMode === 'horizontal';
    };

    const isSlim = () => {
        return menuMode === 'slim';
    };

    /* let topbarItemClick = false;*/
    let containerClassName = classNames('layout-wrapper', {
        'menu-layout-static': menuMode !== 'overlay',
        'menu-layout-overlay': menuMode === 'overlay',
        'layout-menu-overlay-active': overlayMenuActive,
        'menu-layout-slim': menuMode === 'slim',
        'menu-layout-horizontal': menuMode === 'horizontal',
        'layout-menu-static-inactive': staticMenuDesktopInactive,
        'layout-menu-static-active': staticMenuMobileActive,
        'p-input-filled': inputStyle === 'filled',
        'p-ripple-disabled': !ripple,
    });

    const menu = [
        {

        }
    ]

    const onRootMenuitemClick = () => {
        setMenuActive(prevMenuActive => !prevMenuActive);
    };

    const onMenuitemClick = (event) => {
        if (!event.item.items) {
            hideOverlayMenu();
        }

        if (!event.item.items && (isSlim() || isHorizontal())) {
            setMenuActive(false);
        }
    };

    const hideOverlayMenu = () => {
        setOverlayMenuActive(false);
        setRotateMenuButton(false);
        setStaticMenuMobileActive(false);
    };
    

    const onTopbarItemClick = (e) => {
        /* topbarItemClick = true; */

        if (activeTopbarItem === e.item) setActiveTopbarItem(null);
        else setActiveTopbarItem(e.item);

        e.originalEvent.preventDefault();
    };

    const onTopbarMenuButtonClick = (event) => {
        /* topbarItemClick = true;*/
        setTopbarMenuActive((prevTopbarMenuActive) => !prevTopbarMenuActive);
        event.preventDefault();
    };

    const isDesktop = () => {
        return window.innerWidth > 1024;
    };


    const onMenuButtonClick = (event) => {
        setRotateMenuButton(prev => !prev);
        setTopbarMenuActive(false);

        if (menuMode === 'overlay') {
            setOverlayMenuActive(prevOverlayMenuActive => !prevOverlayMenuActive);
        }
        else {
            if (isDesktop())
                setStaticMenuDesktopInactive(prevStaticMenuDesktopInactive => !prevStaticMenuDesktopInactive);
            else
                setStaticMenuMobileActive(prevStaticMenuMobileActive => !prevStaticMenuMobileActive);
        }

        event.preventDefault();
    };

    const routers = [
        {
            path: '/agregarCampania',
            component: AgregarCampania
        },
        {
            path: '/listadoCampanias',
            component: ListadoCampanias
        },
        {
            path: '/detalleCampania/:id',
            component: ListadoCampanias
        }
    ];

    return (
        <div className={containerClassName}>
            
            <div className="layout-wrapper-content">
                <AppTopbar authenticatedUser={{}} topbarMenuActive={topbarMenuActive} profileMode={profileMode} horizontal={isHorizontal()} activeTopbarItem={activeTopbarItem} rotateMenuButton={rotateMenuButton}
                    onMenuButtonClick={onMenuButtonClick} onTopbarMenuButtonClick={onTopbarMenuButtonClick} onTopbarItemClick={onTopbarItemClick}></AppTopbar>
                <div className="layout-menu-container">
                    <div className="menu-scroll-content">
                        <AppMenu model={menu} menuMode={menuMode} active={menuActive} onMenuitemClick={onMenuitemClick} onRootMenuitemClick={onRootMenuitemClick}></AppMenu>
                    </div>
                </div>
                <div className="layout-main">
                    {routers.map((router, index) => {
                        if (router.exact) {
                            return <Route key={`router${index}`} path={router.path} exact component={router.component} />;
                        }

                        return <Route key={`router${index}`} path={router.path} component={router.component} />;
                    })}
                </div>
            </div>
        </div>

    )
}

export default withRouter(App);
