import React, { useEffect, useState, useRef } from 'react'
import { Card } from 'primereact/card';
import { InputText } from 'primereact/inputtext';
import { Button } from 'primereact/button';
import { Dropdown } from 'primereact/dropdown';
import { DataTable } from 'primereact/datatable';
import { Column } from 'primereact/column';
import { MultiSelect } from 'primereact/multiselect';
import { Toast } from 'primereact/toast'; 
import 'react-toastify/dist/ReactToastify.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import { Calendar } from 'primereact/calendar';
import { InputTextarea } from 'primereact/inputtextarea';
import { format } from "date-fns";
import OfertaComercialService from '../../services/OfertaComercialService';
import { InputSwitch } from 'primereact/inputswitch';
import { InputNumber } from 'primereact/inputnumber';

export const AgregarCampania = () => {

    const [dataListCart, setDataListCart] = useState([]);
    const [selectedTipoProducto, setSelectedTipoProducto] = useState();
    const [selectedCanal, setSelectedCanal] = useState();
    const [selectedFuncionalidad, setSelectedFuncionalidad]= useState();
    const [selectedVariables, setSelectedVariables]= useState();
    const [selectedComparador, setSelectedComparador]= useState();
    const [tipoProducto, setTipoProducto] = useState();
    const [canal, setCanal] = useState();
    const [nombre, setNombre] = useState();
    const [remitente, setRemitente] = useState();
    const [mensaje, setMensaje] = useState();
    const [campos, setCampos] = useState();
    const [listFuncionalidad, setListFuncionalidad] = useState([]);
    const [valor, setValor] = useState();
    const [date, setDate] = useState(null);
    const [hora, setHora] = useState(null);
    const service = new OfertaComercialService();
    const [count, setCount] = useState(1);
    const [value12, setValue12] = useState(null);
    const [comparadores, setComparadores] = useState();
    const [BeneficioMTH, setBeneficioMTH] = useState(false);
    const [BeneficioRecarga, setBeneficioRecarga] = useState(false);
    const [OverrideListaNegra, setOverrideListaNegra] = useState(false);
    const [Prioridad, setPrioridad] = useState(false);
    const [OverrideMax, setOverrideMax] = useState(false);
    const [GrupoControl, setGrupoControl] = useState(1);
    const toast = useRef(null);
    const formRef = useRef(null);
  
    useEffect(() => {
        onloadTipoProducto();
        onloadCanalEnvio();
        onloadFuncionalidad();
    }, []);

    const onloadTipoProducto = async () => {
        let res = await service.getTipoProducto();
        console.log(res);
        setTipoProducto(res);
    }
   
    const onloadCanalEnvio = async () => {
        let res = await service.getCanal();
        console.log(res);
        setCanal(res);
    }

    const onloadFuncionalidad = async () => {
        let func = await service.getFuncinalidad();
        console.log(func);
        setListFuncionalidad(func);
    }

   const onFuncionalidadSelect = async (dep) => {
        setSelectedFuncionalidad(dep)
        let res = await service.getCampos(dep.id)
        setCampos(res)
        console.log(res);
    }
   
   
    const onVariablesSelect = async (tipo) => {
        console.log(tipo.tipo)
        setSelectedVariables(tipo)
        if (tipo.tipo === 'STRING') {
            const  data = [
                { label: '=', value: '=' },
                { label: 'like', value: 'like' },
                { label: 'IN', value: 'IN' }
            ];
            setComparadores(data);
        } else {
            const  data = [
                { label: '>', value: '>' },
                { label: '<', value: '<' },
                { label: '>=', value: '>=' },
                { label: '<=', value: '<=' },
                { label: '=', value: '=' },
                { label: 'IN', value: 'IN' }
            ]
            setComparadores(data);
        }
        console.log(tipo);
    }

 
    const CamposConsulta = [
        { nombre: 'Numero Telefono', value: 'num_telefono' },
        { nombre: 'Rango Antiguedad', value: 'rango_antiguedad' },
        { nombre: 'Edad', value: 'edad' },
        { nombre: 'Usuario Movistar', value: 'usuario_mov' },
        { nombre: 'Ultima recarga', value: 'ultima_recarga' }
    ]

    const actionBodyTemplate = (rowData) => {
        return (
            <React.Fragment>
                <Button icon="pi pi-pencil" className="p-button-rounded p-button-success mr-1" onClick={() => console.log(rowData)} />
                <Button icon="pi pi-trash" className="p-button-rounded p-button-warning" onClick={() => onDeleteItem(rowData)} />
            </React.Fragment>
        );
    }

    const onDeleteItem=(record)=>{
        setDataListCart(pre=>{
            return pre.filter((item) => item.id !== record.id);
        })
    }

    const getItemValues = () => {
        let cont = [];
        dataListCart.items.forEach((element, index) => {
            cont.push({
                linea: index,
                function: element.funcionalidad,
                variable: element.variable,
                comparator: element.comparador
            })
        });
        return cont;
    }

    const handleCampania = (event) => {
       event.preventDefault() 
       console.log('handleing campaa');
       const formatted = date.map((date1) => format(date1, "yyyy-MM-dd"));
       console.log(formatted);

       const obj = {
        nombre: nombre,
        tipoProductoId: selectedTipoProducto.id,
        tipoCanalId: selectedCanal.id,
        campos: value12,
        mensaje:mensaje,
        remitente:remitente,
        fchInicio: formatted,
        prioridad: Prioridad,
        horaInicio:  format(hora, "H:mm"),
        detalle: dataListCart
        }
    
        console.log(obj);

        service.setCampanias(obj).then(response => {
           // console.log("response: ", JSON.stringify(response) )  
            if(response.status === 200){
                toast.current.show({ severity: 'success', summary: 'Creacin de Campaas', detail: 'Campaa creado con Exito !!!', life:7000 });
                document.getElementById('formularioCampania').reset()
            }else{
                toast.current.show({ severity: 'error', summary: 'Creacin de Campaas', detail: 'Error en la creacion' , life:7000 });
            }
        } )
    }

//funcion agregar filtros al listado de filtros
    function handleSubmit(e) {
        e.preventDefault();
        setCount(count + 1);
       //si variable es tipo string agrega comillas
        if(selectedVariables.tipo === 'STRING'){
            const newItem ={ 
                id: count,
                funcionalidad: selectedFuncionalidad.descripcion,
                variables: selectedFuncionalidad.tablaReferencia+'.'+selectedVariables.nombre+' '+selectedComparador+' '+'\''+valor+'\''
            }
            setDataListCart([...dataListCart, newItem]);

        }else{
            const newItem ={ 
                id: count,
                funcionalidad: selectedFuncionalidad.descripcion,
                variables: selectedFuncionalidad.tablaReferencia+'.'+selectedVariables.nombre+' '+selectedComparador+' '+valor
            }
            setDataListCart([...dataListCart, newItem]);
        }


        if (selectedComparador === 'IN') {
            if(selectedVariables.tipo === 'STRING'){
                var array2=[];
                for (let letter of valor.split(',')) {
                    array2.push('\''+letter+'\'')
                  }
                 
                const  newItem2 ={ 
                    id: count,
                    funcionalidad: selectedFuncionalidad.descripcion,
                    variables: selectedFuncionalidad.tablaReferencia+'.'+selectedVariables.nombre+' '+selectedComparador+' ('+array2+')'
                }
                setDataListCart([...dataListCart, newItem2]);
            }else{
                const  newItem2 ={ 
                    id: count,
                    funcionalidad: selectedFuncionalidad.descripcion,
                    variables: selectedFuncionalidad.tablaReferencia+'.'+selectedVariables.nombre+' '+selectedComparador+' ('+valor+')'
                }
                setDataListCart([...dataListCart, newItem2]);
            }
           
        }
          
      }

    
    return (
    <div className="container-fluid">
    
     <Toast ref={toast} />
     <Card className="col-12">
            <form id='formularioCampania'  onSubmit={handleCampania} ref={formRef}>
            <div className='row'>     
            <div className="grid">
            <div className="col-12 col-lg-9">
                  <div className='card p-fluid' > 
                    <div className='col-12 center' >
                        <h4>Datos de Campaas</h4>
                        <div className='p-divider p-component p-divider-horizontal p-divider-solid p-divider-left'></div>
                    </div>
                    <br/>
                    <div className="grid">
                        <div className="col-8">
                        <span className="p-float-label"> 
                            <InputText id="nombre" value={nombre} onChange={(e) => setNombre(e.target.value)} required/>
                            <label htmlFor="nombre">Nombre Campaa</label>
                            </span>    
                        </div>
                        <div className="col-2">
                        <span className="p-float-label"> 
                                <Dropdown id="producto" required optionLabel="descripcion" value={selectedTipoProducto} options={tipoProducto} onChange={(e) => setSelectedTipoProducto(e.value)}  />
                                <label htmlFor="producto">Producto</label>
                                </span>      
                        </div>
                        <div className="col-2">
                          <span className="p-float-label"> 
                            <Dropdown id="canalEnvio" optionLabel="descripcion" value={selectedCanal} options={canal} onChange={(e) => setSelectedCanal(e.value)}  required/>
                            <label htmlFor="canalEnvio">Canal</label>
                          </span>   
                        </div>
                    </div>
                    <br></br>

                    <div className='grid'>      
                    <div className="col-8">
                    <span className="p-float-label"> 
                        <Calendar id="range" value={date} onChange={(e) => setDate(e.value)} selectionMode="multiple" required  readOnlyInput />
                        <label htmlFor="range">Rango de Fechas</label>
                      </span>  
                    </div>  
                    <div className="col-2">
                        <span className="p-float-label"> 
                        <Calendar id="time12" value={hora} onChange={(e) => setHora(e.value)} timeOnly hourFormat="12" required/>
                        <label htmlFor="time12">Hora envio</label>
                        </span>
                    </div>
                    <div className="col-2">
                    <label >Prioridad </label>
                        <InputSwitch id="prioridad" checked={Prioridad} onChange={(e) => setPrioridad(e.value)} />
                    </div>    
                    <div className="col-8">
                        <span className="p-float-label">
                            <MultiSelect id="camposConsulta" value={value12} options={CamposConsulta} onChange={(e) => setValue12(e.value)} optionLabel="nombre" required/>
                            <label htmlFor="multiselect">Campos Consultar</label>
                        </span>
                    </div>
                    <div className="col-4">
                    <span className="p-float-label">
                            <InputText id="remitente" value={remitente} onChange={(e) => setRemitente(e.target.value)} required maxLength={11}/>
                            <label htmlFor="remitente">Remitente</label>
                        </span>
                    </div> 
                    <div className="col-12">
                            <span className="p-float-label"> 
                                <InputTextarea id="mensaje"  value={mensaje} onChange={(e) => setMensaje(e.target.value)}  required/>
                                <label htmlFor="mensaje">Mensaje</label>
                            </span>    
                        </div>   
                    </div>
                  </div>
            </div>

            <div className='col-12 col-lg-3'> 
                <div className='card p-fluid'> 
                    <div className='col-12 center' >
                        <h4>Beneficios</h4>
                        <div className='p-divider p-component p-divider-horizontal p-divider-solid p-divider-left'></div>
                    </div>
                    <br/>
                    <div className="field grid">
                    <label className='col-6 mb-2 md:col-4 md:mb-0'>Beneficio Recarga </label>
                    <div className="col-6 md:col-8">
                      <InputSwitch checked={BeneficioRecarga} onChange={(e) => setBeneficioRecarga(e.value)} />  </div>
                    </div>
                    
                    <div className="field grid">
                    <label className='col-6 mb-2 md:col-4 md:mb-0'>Beneficio MTH </label>
                    <div className="col-6 md:col-8">    <InputSwitch checked={BeneficioMTH} onChange={(e) => setBeneficioMTH(e.value)} /> </div>
                    </div>

                    <div className="field grid">
                    <label className='col-6 mb-2 md:col-4 md:mb-0'>Override Lista Negra </label>
                    <div className="col-6 md:col-8">   <InputSwitch checked={OverrideListaNegra} onChange={(e) => setOverrideListaNegra(e.value)} /> </div>
                    </div>

                    <div className="field grid">
                    <label className='col-6 mb-2 md:col-4 md:mb-0'>Override diario </label>
                    <div className="col-6 md:col-8">   <InputSwitch checked={OverrideMax} onChange={(e) => setOverrideMax(e.value)} /> </div>
                    </div>

                    <div className="field grid">
                    <label className='col-6 mb-2 md:col-4 md:mb-0'>Grupo control</label>
                    <div className="col-6 md:col-8">  
                     <InputNumber inputId="percent" value={GrupoControl} onValueChange={(e) => setGrupoControl(e.value)} prefix="%" showButtons min={0} max={100}/> </div>
                    </div>

                </div>
            </div>
            
            <div className='col-12 col-lg-12'> 
                <div className='card p-fluid'> 
                <div className='col-12 center' >
                        <h4>Criterial</h4>
                        <div className='p-divider p-component p-divider-horizontal p-divider-solid p-divider-left'></div>
                    </div>
                   <div className='row'>  
                        <div className="col-3">
                        <Dropdown optionLabel="descripcion"  value={selectedFuncionalidad} options={listFuncionalidad} onChange={(e) => onFuncionalidadSelect(e.value)} placeholder="Seleccione uno" />
                            <label htmlFor="descripcion">Funcionalidad</label>
                        </div>
                        <div className="col-3">
                        <Dropdown optionLabel="nombre"  value={selectedVariables}  options={campos}  onChange={(e)=>onVariablesSelect(e.value)} />
                            <label htmlFor="variable">Variables</label>
                        </div>
                        <div className="col-2">
                            <Dropdown id='comparador' value={selectedComparador} options={comparadores} onChange={(e) => setSelectedComparador(e.value)} placeholder="Seleccione un comparador" />
                            <label htmlFor="comparador">Comparadores</label>
                        </div>
                        <div className="col-2">
                            <InputText id='valor' value={valor} onChange={(e) => setValor(e.target.value)}/>
                            <label htmlFor="valor">valor</label>
                        </div>
                        <div className='col-2'>
                        <Button label="Agregar filtro"  onClick={(e) => handleSubmit(e)} className="p-button-info" />
                        </div>
                    </div>       
                </div>        
                
            <div className='btn'>
                        <Button type='submit' label="Guardar" className="p-button p-component" />
                    </div>
            </div>
          
                </div>
            </div>        
            </form>

            <DataTable value={dataListCart} header="Listado de filtros" responsiveLayout="stack" breakpoint="960px">
                <Column field="id"     hidden       header="id"></Column>
                <Column field="funcionalidad" header="Funcionalidad"></Column>
                <Column field="variables"      header="Variable"></Column>
                <Column field="valor"         header="Valor"></Column>
                <Column body={actionBodyTemplate} exportable={true} style={{ minWidth: '8rem' }}></Column>
                  {/* id:element.id, count: element.count, price: element.price, descripcion: element.plan.descripcion  */}
            </DataTable>       
    </Card>
       
    </div>   
    )
}
