import { environment } from "../environments/environment";

import AxiosImp from "../utils/AxiosImp";

export default class OfertaComercialService {
    axiosImp;

    constructor() {
        this.axiosImp = new AxiosImp();
    }

    getTipoProducto(){
        return this.axiosImp.get(environment.END_POINTS.GET_TIPO_PRODUCTO);
    }
    getCanal(){
        return this.axiosImp.get(environment.END_POINTS.GET_CANAL);
    }
    getFuncinalidad(){
        return this.axiosImp.get(environment.END_POINTS.GET_FUNCIONALIDAD);
    }
    getCampos(idFuncionalidad){
        return this.axiosImp.get(environment.END_POINTS.GET_CAMPOS+`/${idFuncionalidad}`);
    }

    getCampanias(){
        return this.axiosImp.get(environment.END_POINTS.GET_CAMPANIAS);
    }

    setCampanias(orden){
        return this.axiosImp.post(environment.END_POINTS.GET_CAMPANIAS_ADD,orden)
    }

    deleteCampanias(id){
        return this.axiosImp.delete(environment.END_POINTS.DELETE_CAMPANIAS+`/${id}`);
    }

    updateCampanias(id){
        return this.axiosImp.put(environment.END_POINTS.UPDATE_CAMPANIAS+`/${id}`);
    }
}
