import { createSlice } from '@reduxjs/toolkit'

export const cartSlice = createSlice({
    name: 'cart',
    initialState: {
        itemCount: 0,
        items: []
    },

    reducers: {
        addItem: (state, action) => {

            console.log('este es item ', action.payload);
            action.payload = {
                ...action.payload, 
                copia: false,
                tipoServicio: '',
                tipoTelefono: '',
            }

            if (state.items.filter(i => i.id === action.payload.id).length > 0) {
                state.items.map(i => {
                    if (i.id === action.payload.id)
                        return { ...i, count: i.count++ }
                    else
                        return i
                })
                return
            }
            state.items.push({ ...action.payload, count: 1 })
        },
        increment: (state) => {
            state.itemCount += 1
        },
        decrement: (state) => {
            state.itemCount -= 1
        },
        removeItem: (state, action) => {
            if (state.itemCount === 0) {
                state.items = []
                return
            }
            if (state.items.filter(i => i.id === action.payload.id).length > 0) {
                console.log("miremos pues: ", action.payload.id)
                state.items.map(i => {
                    if (i.id === action.payload.id)
                        return { ...i, count: i.count-- }
                    else
                        return i
                })
                return
            } else {
                state.items = state.items.filter(i => i.id !== action.payload.id);
                state.itemCount--;
            }

        },
        removeAll: (state) => {
            state.items = []
            state.itemCount = 0
            return
        },
        edit: (state, action) =>{
            action.payload = {
                ...action.payload, 
            }
            let indextoChange = state.items.findIndex(i=> i.id === action.payload.id);
            state.items[indextoChange] = action.payload;
            state.itemCount = action.payload.count;
        },
        changeTipoServicio: (state, action) =>{

            console.log('payload',action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            console.log(indexToChange);
            state.items[indexToChange] ={...state.items[indexToChange], tipoServicio:action.payload.idDrop};
        },
        changeTipoPlan: (state, action) =>{
            console.log(action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            state.items[indexToChange] = {...state.items[indexToChange], tipoTelefono:action.payload.val};
        },
        updatePrecioFinanciado: (state, action) =>{
            console.log(action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            state.items[indexToChange] = {...state.items[indexToChange], precioTerminal:action.payload.val};
        },
        updateDireccion: (state, action) =>{
            console.log(action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            
        },
        addExtras: (state, action) =>{
            console.log(action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            state.items[indexToChange] = {...state.items[indexToChange], extras:action.payload.val};
        },
        addPrices: (state, action) =>{
            console.log(action.payload);
            let indexToChange = state.items.findIndex(i => i.id === action.payload.id);
            switch (action.payload.tipoPrecio) {
                case 'precioProteccion':
                    state.items[indexToChange] = {...state.items[indexToChange], pricePro:action.payload.val};
                    break;
                case 'precioFinanciado':
                    state.items[indexToChange] = {...state.items[indexToChange], pricePlan:action.payload.val};
                    break;
                case 'precio':
                    state.items[indexToChange] = {...state.items[indexToChange], price:action.payload.val};
                    break;
            
                default:
                    break;
            }
        }

    }

})

export const selectTotal = (state) => state.cart.items.length > 0 ? state.cart.items.reduce((acc, value) => acc + (value.price * value.count), 0) : 0
export const selectItemCount = (state) => state.cart.itemCount
export const selectItems = (state) => state.cart.items
export const { addItem, increment, removeItem, decrement, removeAll ,edit , changeTipoServicio , changeTipoPlan , updatePrecioFinanciado,addPrices} = cartSlice.actions
export default cartSlice.reducer;
