import React, { useState, useEffect, useRef } from 'react';
import { DataTable } from 'primereact/datatable';
import { Tag } from 'primereact/tag';
import { Column } from 'primereact/column';
import { Button } from 'primereact/button';
import { Dialog } from 'primereact/dialog';
import { InputText } from 'primereact/inputtext';
import OfertaComercialService from '../../services/OfertaComercialService';
//import { Link } from 'react-router-dom';
import { Toast } from 'primereact/toast'; 

export const ListadoCampanias = () => {

    const service = new OfertaComercialService();
    const [products, setProducts] = useState([]);
    const [product, setProduct] = useState([]);
    const [selectedProducts, setSelectedProducts] = useState(null);
    const [deleteProductDialog, setDeleteProductDialog] = useState(false);
    const dt = useRef(null);
    const toast = useRef(null);
    const [expandedRows, setExpandedRows] = useState(null);
    const [globalFilter, setGlobalFilter] = useState(null);
   
    useEffect(() => {
        getListaOrdenes()
    }, []); 


    const getListaOrdenes = async () => {
        let res = await service.getCampanias();
        setProducts(res);
    }
    
    const refreshData = ()=>{
        console.log("refresh data..")
        getListaOrdenes()
    }

    const getSeverity = (status) => {
        switch (status) {
            case 'ERROR':
                return 'danger';

            case 'FINALIZADO':
                return 'success';

            case 'INICIAL':
                return 'info';

            case 'PROCESANDO':
                return 'warning';

            case 'renewal':
                return null;
        }
    };

    const confirmDeleteProduct = (obj) => {
        setProduct(obj);
        setDeleteProductDialog(true);
        console.log(obj)
    }

    const hideDeleteProductDialog = () => {
        setDeleteProductDialog(false);
    }

    const deleteSelectedProducts = () => {
        let _products = products.filter(val => val.id !== product.id);
        setProducts(_products);
        setDeleteProductDialog(false);
        service.deleteCampanias(product.id);
        console.log(product.id)
    }
    
    const updateEjecution = async (rowData) => {
        console.log("rowData: ",rowData)
        service.updateCampanias(rowData.id).then(response => {
            console.log("response: ",response)
             if(response){
                getListaOrdenes();
                 toast.current.show({ severity: 'success', summary: 'Modificacin de Campaas', detail: 'Campaa se ha modificado con Exito !!!', life:6000 });
             }else{
                 toast.current.show({ severity: 'error', summary: 'Modificacin de Campaas', detail: 'Error en la Modificacin' , life:6000 });
             }
         } )
        
    }

    const actionBodyTemplate = (rowData) => {
        return (
            <React.Fragment>
                <Button icon="pi pi-check" className="p-button-rounded p-button-success p-button-outlined"  onClick={() => updateEjecution(rowData)} tooltip='Ejecutar' />
                <Button icon="pi pi-stop-circle"  className="p-button-rounded p-button-danger" onClick={() => confirmDeleteProduct(rowData)} tooltip='Eliminar'/>
            </React.Fragment>
        );
    }

    const deleteProductDialogFooter = (
        <React.Fragment>
            <Button label="No" icon="pi pi-times" className="p-button-text" onClick={hideDeleteProductDialog} />
            <Button label="Si" icon="pi pi-check" className="p-button-text" onClick={deleteSelectedProducts} />
        </React.Fragment>
    );
    
    const statusBodyTemplate = (rowData) => {
        return <Tag value={rowData.estado} severity={getSeverity(rowData.estado)} />;
    };

    const header = (
        <div className="table-header">
           
        <div className="flex flex-wrap align-items-center justify-content-between gap-2">
         <span className="p-input-icon-right">
                <i className="pi pi-search" />
                <InputText type="search"  onInput={(e) => setGlobalFilter(e.target.value)} placeholder="Buscar..." />
            </span>
         <Button icon="pi pi-refresh" rounded raised  onClick={() => refreshData()} />
     </div>
        </div>
     
    );

    const allowExpansion = (rowData) => {
        return rowData.orders.length > 0;
    };
    
    const onRowExpand = (event) => {
        toast.current.show({ severity: 'info', summary: 'Campaa Expanded', detail: event.data.name, life: 3000 });
    };

    const onRowCollapse = (event) => {
        toast.current.show({ severity: 'success', summary: 'Campaa Collapsed', detail: event.data.name, life: 3000 });
    };

    const rowExpansionTemplate = (data) => {
        return (
            <div className="p-3">
              <h5><b>Mensaje:</b></h5>  {data.mensaje} 
             <br></br>
              <h5><b>Filtros:</b></h5>  {data.filtros}
            </div>
        );
    };


    return (
        <div>
        <div className="container">
        <div className="card">
        <Toast ref={toast} />
        <DataTable ref={dt} value={products} selection={selectedProducts} onSelectionChange={(e) => setSelectedProducts(e.value)}
          expandedRows={expandedRows} onRowToggle={(e) => setExpandedRows(e.data)}
          onRowExpand={onRowExpand} onRowCollapse={onRowCollapse} rowExpansionTemplate={rowExpansionTemplate}
                    dataKey="id" paginator rows={10} rowsPerPageOptions={[5, 10, 25]}
                    paginatorTemplate="FirstPageLink PrevPageLink PageLinks NextPageLink LastPageLink CurrentPageReport RowsPerPageDropdown"
                    currentPageReportTemplate="Mostrando {first} de {last} de {totalRecords} Items"
                    globalFilter={globalFilter} header={header} responsiveLayout="scroll">
                <Column expander={allowExpansion} style={{ width: '5rem' }} />       
                <Column field="id" header="Id" sortable></Column>
                <Column field="nombre" header="Nombre" sortable filter filterPlaceholder="Buscar por nombre" ></Column>
                <Column field="fchInicio" header="Fecha Inicio" sortable></Column>
                <Column field="horaInicio" header="Hora Inicio" sortable></Column>
                <Column field="fchFin" header="Fecha Finalizada" sortable></Column>
                <Column field="estado" header="Estado"  frozen className="font-bold" body={statusBodyTemplate} sortable filter ></Column>
                <Column body={actionBodyTemplate} exportable={true} style={{ minWidth: '8rem' }}></Column>
            </DataTable>
        </div>

        <Dialog visible={deleteProductDialog} style={{ width: '450px' }} header="Confirmacin" modal footer={deleteProductDialogFooter} onHide={hideDeleteProductDialog}>
                <div className="confirmation-content">
                    <i className="pi pi-exclamation-triangle mr-3" style={{ fontSize: '2rem'}} />
                    {product && <span>Esta seguro de eliminar la campaa <b>{product.name}</b>?</span>}
                </div>
        </Dialog>

        </div>
    </div>
    );
}
